.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,ol,li{
  list-style: none;
}
a{
  text-decoration: none;
  color: inherit;
}
img{
  vertical-align: top;
  max-width: 100%;
}
#header{
  width: 100%;
  height: 122px;
  position: relative;
  z-index: 10;
  background: transparent;
}
#header .up{
  width: 1100px;
  height: 78px;
  margin: 0 auto;
  position: relative;
}
.left{
  width: 250px;
  float: left;
  font-size: 14px;
  position: relative;
}
.language{
  position: absolute;
  width: 250px;
  height: 35px;
  line-height: 35px;
  left: 0;
  top: 10px;
  z-index: 20;
}
.language>a{
  position: absolute;
  width: 250px;
  height: 22px;
  left: 0;
  top: 6px;
  line-height: 22px;
  color: #fff;
  text-align: left;
  padding-left: 30px;
}
.language>a>span{
  display: inline-block;
  width: 14px; height: 8px;
  background: url(../public/images/h_0.png);
  margin-left: 15px;
}
#header .language>a>span>img{
  display: none;  
}
#header:hover .language a{
  color: #333;
}
#header:hover .language>a>span{
  background: none;
}
#header:hover .language>a>span>img{
  display: block;
}

.list{
  position: absolute;
  width: 200px;
  background: #fff;
  font-size: 12px;
  text-align: left;
  padding: 10px 0;
  left: 0;
  top: 45px;
  line-height: 26px;
  border: 1px solid #000;
  z-index: 21;
}
.list li:hover{
  background: #ccc;
}
.list li a{
  padding: 10px;
}
#logo{
  width: 222px;
  height: 42px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#logo a{
  display: block;
  height: 42px;
  background: url(../public/images/logo_1.png) no-repeat left top/cover;
}
.util{
  float: right;
}
.util ul{
  display: flex;
  margin-top: 20px;
  font-size: 14px;
}
.util ul li{
  padding: 0 10px;
  border-right: 1px solid #ccc;
}
.util ul li:last-child{
  border-right: none;
}
.util ul li a{
  color: #fff;
}
.util ul li:last-child a{
  display: block;
  text-indent: -9999px;
  width: 33px;
  height: 18px;
  background: url(../public/images/ser_0.png) no-repeat center/auto;
}

#header:hover{
  background: #fff;
}

#header:hover a{
  color: #666;
}
#header:hover #logo a{
  background-image: url(../public/images/logo_0.png);
}
#header:hover .util li:last-child a{
  background-image: url(../public/images/ser_1.png);
}


#nav{
  height: 44px;
  text-align: center;
  position: relative;
}
#nav>ul{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 44px;
}
#nav>ul>li{
  padding: 0 15px;
}
#nav>ul>li>a{
  display: block;
  height: 44px;
  line-height: 44px;
  color: #fff;
  position: relative;
}
#nav>ul>li>a:after{
  content: '';
  display: block;
  position: absolute;
  width: 0%;
  height: 4px;
  background: #333;
  left: 0;
  bottom: 0;
  transition: all .3s;
}
#nav>ul>li>a:hover:after{
  width: 100%;
}
#nav .subback{
  position: absolute;
  width: 100%;
  background: #fff;
  left: 0;
  text-align: right;
  z-index: 10;
  padding: 10px 0;
  border-top: 1px solid #aaa;
  display: none;
}
#nav>ul>li:hover .subback{
  display: block;
}
#nav .subback .subin{
  width: 1100px;
  margin: 0 auto;
}
#nav .subback .subin .sub{
  width: 500px;
  float: right;
  text-align: left;
  margin-right: 90px;
  display: flex;
  gap: 20px;  
  font-size: 14px;
  line-height: 24px;
}
#nav>ul>li:nth-child(6) .subback .subin .sub{
  margin-right: 100px;
}
#nav>ul>li:nth-child(7) .subback .subin .sub{
  margin-right: 5px;
}
#nav>ul>li:nth-child(8) .subback .subin .sub{
  width: 100%;
  text-align: center;
  margin-right: -310px;
}
#nav>ul>li:nth-child(8) .subback .subin .sub>li:nth-child(2) ol{
  font-size: 14px;  
}
#nav>ul>li:nth-child(8) .subback .subin .sub>li:nth-child(2) ol a{
  color: #ccc;
  line-height: 20px;
}
#nav>ul>li:nth-child(8) .subback .subin .sub>li:nth-child(2) ol a:hover{
  color: #000;
}
#nav .subback .subin .sub>li>a{
  color: #ccc;
}
#nav .subback .subin .sub>li>a:hover{
  color: #000;
}

.main{
  width: 100%; height: 676px;
  position: relative; top: -122px;
  overflow: hidden;
}
.main .imgB{
  width: 100%; height: 676px;
}
.main .imgB img{
   width: 100%; height: 676px;
   object-fit: cover;
}
.main .txtB{
  position: absolute; left: 50%; bottom: 160px;
  text-align: left;
  transform: translateX(-50%);
  width: 1440px; height: 80px;
  opacity: 0;
  animation-name: op;
  animation-duration: 3s;
  animation-delay: .7s;
  animation-iteration-count: infinite;
   animation-fill-mode: backwards;
}

@keyframes op {
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
}
.main .txtB p:nth-child(1){
  font-size: 18px; 
  height: 27px;
  color: #333;
}
.main .txtB p:nth-child(2){
  font-size: 36px; 
  height: 53px;
}
.main .li:nth-child(2) .txtB p,
.main .li:nth-child(3) .txtB p,
.main .li:nth-child(5) .txtB p{
  color: #fff;
}
.s-next,
.s-prev{
  position: absolute; top: 50%;
  transform: translateY(-50%);
  width: 68px;
  height: 68px;
  background: url(../public/images/arr_l.png);
  z-index: 10;
  cursor: pointer;
}
.s-prev{
  left: 30px;
}
.s-next{
  right: 30px;
  background: url(../public/images/arr_r.png);
}

.section{
  width: 1440px;
  height: auto;
  margin: 0 auto;
}
#section1{
  height: 706px;

}
#section1 h2{
  text-align: left;
  width: 1440px; height: 62px;
  font-size: 28px;
  padding-bottom: 20px;
}
#section1 .boxWrap{
  height: 670px;
}
#section1 .boxWrap .slide{
  width: 1440px;
  height: 600px;
  padding: 20px 0;
  overflow: hidden;
}
#section1 .boxWrap .slide .img{
  width: 464px; height: 464px;
  margin-bottom: 24px;
}
#section1 .boxWrap .slide .img img{
  width: 100%;
}
#section1 .boxWrap .slide .txt{
  width: 464px; height: 94px;
}
#section1 .boxWrap .slide .txt p:nth-child(1){
  height: 30px; font-size: 20px;
  font-weight: 700;
}
#section1 .boxWrap .slide .txt p:nth-child(2){
  height: 40px; font-size: 16px;
  color: #888;
}
#section1 .boxWrap .slide .txt p:nth-child(2){
  height: 24px; font-size: 16px;
  font-weight: 500;
}
#section1 .swiper-button-next,
#section1 .swiper-button-prev{
  top: 240px;
}

#section2{
  height: 540px;
  margin-top: 120px;
  position: relative;
}
#section2 .txt{
  position: absolute; left: 170px; bottom: 80px;
  width: 270px;
  height: 174px;
  text-align: left;
  color: #fff;
}
#section2 .txt h2{
  font-size: 32px;
  width: 270px;
  height: 48px;
}
#section2 .txt p{
  width: 270px;
  height: 61px;
  font-size: 18px;
  padding-top: 7px;
  letter-spacing: -1px;
  line-height: 26px;
}
#section2 .txt span{
  display: block;
  width: 114px; height: 39px;
  padding: 8px 23px;
  border: 1px solid #fff;
  font-size: 14px;
  letter-spacing: -2px;
}
#section3{
  height: 651px;
  margin-top: 120px;
}
#section3 h2{
  width: 1440px; height: 62px;
  font-size: 28px;
  text-align: left;
  padding-bottom: 20px;
}
#section3 .boxWrap{
  height: 589px;
}
#section3 .boxWrap ul{
  width: 1440px; height: 522px;
  display: flex; justify-content: space-between;
  overflow: hidden;
}
#section3 .boxWrap ul li:nth-child(1),
#section3 .boxWrap ul li:nth-child(3){
  width: 342px; height: 522px;
  position: relative;
  background: #ededed;
}
#section3 .boxWrap ul li:nth-child(2){
  width: 798px; height: 522px;
  position: relative;
}
#section3 .boxWrap ul li:nth-child(1) .img,
#section3 .boxWrap ul li:nth-child(3) .img{
  width: 342px; height: 342px;
  position: absolute; left: 0; top: 0;
  padding: 12px;
}
#section3 .boxWrap ul li:nth-child(2) .img{
  position: absolute; left: 0; top: 0;
  width: 798px; height: 522px;
}
#section3 .boxWrap ul li:nth-child(1) .txt,
#section3 .boxWrap ul li:nth-child(3) .txt{
  position: absolute; 
  width: 342px; height: 96px;
  left: 0; bottom: 10px;
}
#section3 .boxWrap ul li:nth-child(2) .txt{
  position: absolute; 
  width: 708px; height: 118px;
  left: 0; bottom: 0;
  text-align: left;
}
#section3 .boxWrap ul li:nth-child(1) .txt p:nth-child(1),
#section3 .boxWrap ul li:nth-child(3) .txt p:nth-child(1){
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 3px;
  width: 342px; height: 33px;
}
#section3 .boxWrap ul li:nth-child(1) .txt p:nth-child(2),
#section3 .boxWrap ul li:nth-child(3) .txt p:nth-child(2){
  font-size: 15px;
  padding-bottom: 15px;
  color: #888;
  width: 342px; height: 39px;
}
#section3 .boxWrap ul li:nth-child(1) .txt p:nth-child(3),
#section3 .boxWrap ul li:nth-child(3) .txt p:nth-child(3){
  width: 342px; height: 24px;
  font-weight: 500;
  font-size: 16px;
}
#section3 .boxWrap ul li:nth-child(2) .txt p:nth-child(1){
  width: 708px; height: 39px;
  font-size: 20px; font-weight: 700;
  padding: 3px 0 3px 62px;
}
#section3 .boxWrap ul li:nth-child(2) .txt p:nth-child(2){
  width: 708px; height: 79px;
  font-size: 15px;
  padding: 7px 0 48px 62px;
  color: #666;
}

#section4{
  width: 1440px; height: 718px;
  overflow: hidden;
  margin-top: 120px;
}
#section4 ul{
  width: 1440px; height: 718px;
  display: flex; justify-content: space-between;
}
#section4 ul li{
  width: 707px; height: 718px;
  color: #222;
}
#section4 ul li .img{
  height: 599px;
}
#section4 ul li .txt{
  height: 119px;
}
#section4 ul li .txt h3{
  width: 707px; height: 78px;
  font-size: 28px;
  padding: 22px 0 14px;
}
#section4 ul li .txt span{
  display: inline-block;
  width: 134px; height: 41px;
  font-size: 14px;
  padding: 9px 33px;
  border: 1px solid #222;
  letter-spacing: -2px;
}
#footer{
  height: 156px;
  margin-top: 80px;
  padding-bottom: 37px;
  font-size: 12px;
  color: #767676;
  border-top: 1px solid #767676;
}
#footer .fup{
  height: 55px;
}
#footer .fup .footIn{
  width: 1200px; height: 55px;
  padding: 0 50px;
  margin: 0 auto;
  display:flex ; justify-content: space-between;
  align-items: center;
}
#footer .fup .footIn .fmenu{
  width: 820px; height: 55px;
}
#footer .fup .footIn .fmenu ul{
  width: 820px; height: 55px;
  display: flex;
  align-items: center;
}
#footer .fup .footIn .fmenu ul li a{
  display: block; 
  padding: 3px 14px 3px 0;
  border-right: 1px solid #767676;
  margin-left: 14px;
}
#footer .fup .footIn .fmenu ul li a:hover{
  text-decoration: underline;
}
#footer .fup .footIn .fmenu ul li:first-child a{
  margin-left: 0;
}
#footer .fup .footIn .fmenu ul li:nth-child(3) a{
  font-weight: 700;
  font-size: 13px;
  color: #000;
}
#footer .fup .footIn .fmenu ul li:last-child a{
  border: 0;
}
#footer .fup .fsns{
  width: 240px; height: 55px;
}
#footer .fup .fsns ul{
  width: 240px; height: 55px;
  display: flex; justify-content: space-between;
  align-items: center;
}
#footer .fup .fsns ul li{
  width: 30px; height: 30px;
}
#footer .fup .fsns ul li:first-child{
  width: 42px; height: 28px;
}
#footer .fdown{
  height: 63px; border-top: 1px solid #767676;
}
#footer .fdown .footIn{
  width: 1200px; height: 63px;
  text-align: left;
  margin: 0 auto;
  padding: 0 50px;
}
#footer .fdown .footIn .tel{
  width: 820px; height: 43px;
  padding-top: 10px;
  font-size: 15px;
}
#footer .fdown .footIn .copy{
  font-size: 14px;
  height: 20px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
